const TabContent = ({ items }) => {




  const cartItems = useSelector((state) => state?.cart?.items, shallowEqual);
  const ss = useSelector((state) => state, shallowEqual);
  const dispatch = useDispatch();
  React.useEffect(() => {}, []);

  const onUpdate = (productData) => {
    const action = updateCart({
      id: productData.productId,
      quantity: productData.quantity,
      comments: productData.comments
    });
    dispatch(action);
  };

  return (
    <div className="row">
      {items.map((item, i) => (
        <div key={i} className="col-sm-4">
          <ProductCard data={item} onUpdate={onUpdate} />

        </div>
      ))}
    </div>
  );
};

